import Router from "vue-router"
import Vue from 'vue'
import Home from '../pages/Home'
import Login from '../pages/login'
import App from '../App'
import goPage from '../components/goPage'
Vue.use(Router)

const routes  = [
    {
        path: '/',
        component: App
    },
    {
        path: '/home',
        component: Home
    },
    {
        path: '/login',
        component: Login
    },
    {
      path: '/openapp',
      component: goPage
    }
]

export default new Router({
    routes
})



