<template>
    <div class="btn-container" v-if="isShow">
        <div class="btn download">
            <button ref="copy" :data-clipboard-text="copyValue" @click="goApp">App内下载</button>
        </div>
        <div class="btn save">
            <button @click="saveToCloud">转存到网盘</button>
        </div>
    </div>
</template>

<script>
import Clipboard from 'clipboard'
 import { isQQBrowser } from '../test/result'
 import { _openApp } from '../utils/app'
export default {
  name: "saveButton",
  props: {
  isShow: {
    type: Boolean,
    default: false
  }
},
  data() {
    return {
      copyValue: '',
      copyBtn: '',
      outlinkUrl: 'http://caiyun.feixin.10086.cn/dl/'
    }
  },
  computed: {

  },
  mounted() {
    // let base64Info = this.$router.query.base64Info
    // console.log(`from router ${base64Info}`);
    // if(isQQBrowser()) {
    //   this.$nextTick(()=> {
    //     console.log('this.$refs.copy :',this.$refs.copy);
    //     this.copyBtn = new Clipboard(this.$refs.copy)
    //   })
    //
    // }
  },
  methods: {
    saveToCloud() {
      console.log('button');
      this.$emit("saveToCloud");
    },

    goApp() {
      if(isQQBrowser()) {
        this.$nextTick(() => {
          this.copyBtn = new Clipboard(this.$refs.copy)
          let linkID ='185CkdO1Y7L7W'
          let code = 'inx2Ot'
          this.copyValue = `链接：${this.outlinkUrl}${linkID} 提取码：${code}`
          const clipboard = this.copyBtn
          clipboard.on('success',function() {
            console.log('success');
            _openApp()
          })
          clipboard.on('error',function() {
            console.log('error');
          })
        })
      } else {
        console.log('noqq');
        this.$emit("goApp");
      }
    }
  },
  destroy() {
    this.copyBtn.destroy()
  }
};
</script>

<style scoped>
    .btn-container {
        position: fixed;
        width: 80%;
        z-index: 1;
        bottom: 40px;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
      }
      .btn button{
          width: 100px;
          height: 40px;
          line-height: 40px;
          font-size: 18px;
          border: 1px solid blue;
          border-radius: 10px;
          text-align: center;
      }
      .download button {
            background-color: #fff;
            color: blue;
            box-shadow: 0 6px 12px rgba(0,0,0,0.5);
        }
        .save button{
            background-color: blue;
            color: #fff;
            box-shadow: 0 6px 12px rgba(94,136,155,0.8);
        }
        div.btn button {
          display: block;
          cursor: pointer;
          width: 100%;
          height: 100%;
        }
</style>

  </div>
</template>



